.board {
  width: 50%;
  margin: auto;
  display: flex;
  justify-content: center;
  flex-flow: column;
}

#title {
  font-weight: bold;
  font-size: 30px;
  color: white;
  background-color: #b08941;
}

.team {
  display: flex;
  justify-content:space-between;
  margin-bottom: 5px;
  padding: 5px;
}

.name {
  align-items: flex-start;
  font-weight: 500;
}

.contribution {
  font-weight: 500;
}

.even {
  background-color: #fff9f3;
}
